import React from 'react';
import {useParams} from "react-router-dom";
import {useContext, useEffect, useState} from "react";
import {AuthContext} from "../Context/AuthProvider";
import Header from "../Components/Header";
import AddArtikelField from "../Components/AddArtikelField";
import NavBar from "../Components/NavBar";
import {getListByName} from "../Services/apiService";

export default function ListPage(){
    let params = useParams()

    const [list, setList] = useState<string[]>()
    const [artikel, setArtikel] = useState<string>("")
    const [anzahl, setAnzahl] = useState<number>(0)

    const {token} = useContext(AuthContext)

    useEffect(() => {
        loadArtikel().then(() => console.log(list))
            .catch((e: { message: any; }) => console.log(e.message))
    })

    const loadArtikel = () => getListByName(params.whichList, token)
        .then(setList)

    if (!list){
        return(
            <div>
                <NavBar />
                <Header title="Zettel erstellen"/>

            </div>
        )
    }return(
        <div>
            <NavBar />
            <Header title="Zettel"/>
            <AddArtikelField artikel={artikel} setArtikel={setArtikel} anzahl={anzahl} setAnzahl={setAnzahl} />
        </div>
    )
}